@import '../../lib/sass/px2rem';
@import '../../lib/sass/mixin';

html,
body { background: #f1f0f6;}

.apply-list { padding: 0 q(18);
    .apply-box { margin-top: q(18); padding: 0 q(8); border: 1px solid #ddd; background: #fff;
        .apply-inner-box { display: flex; justify-content: space-between; align-items: center; height: q(90); padding: 0 q(14); box-sizing: border-box; border-bottom: 1px solid #f2f2f2;
            .apply-right { display: flex; align-items: center;
                .right-arrow { width: q(16); height: q(27);}
                .update-time { margin-right: q(14); font-size: q(20); color: #3aa5ff;}
            }
            .apply-title-box { flex: 1; font-size: q(26); color: #666;
                .apply-sec-title { display: inline-block; margin-left: q(18);}
            }
        }
    }
}

.tips-box { padding: 0 q(18);
    .tip-inner-box { margin-top: q(36); line-height: q(36); font-size: q(24); color: #3aa5ff; padding-left: q(50); background: url('../images/tipsIcon.png') left q(3) no-repeat; background-size: q(30) q(30);
        .colon { display: inline-block; margin: 0 q(18) 0 q(5);}
    }
}

